import React, { Component } from 'react';
import NewsItem from './NewsItem';
import Spinner from './Spinner';
export default class News extends Component {


    static defaultProps = {
        category: 'general',
        pageSize: 6,
    }


    articles = [];
    constructor() {
        super();
        this.state = {
            atcs: this.articles,
            loading: false,
            page: 1,
        }
    }
    async componentDidMount() {
        let url = `https://newsapi.org/v2/top-headlines?country=in&category=${this.props.category}&apiKey=acbd56ce647a4069bf7b9a5002f06ffb&pageSize=${this.props.pageSize}`;
        this.setState({ loading: true })
        let data = await fetch(url);
        let parsedData = await data.json();
        this.setState({ atcs: parsedData.articles, totalResults: parsedData.totalResults, loading: false, });
    }

    handlePrevClick = async () => {
        let url = `https://newsapi.org/v2/top-headlines?country=in&category=${this.props.category}&apiKey=acbd56ce647a4069bf7b9a5002f06ffb&page=${this.state.page - 1}&pageSize=${this.props.pageSize}`;
        this.setState({ loading: true })
        let data = await fetch(url);
        let parsedData = await data.json()
        console.log(parsedData);
        this.setState({
            atcs: parsedData.articles,
            page: this.state.page - 1,
            loading: false,
        });
    }
    handleNextClick = async () => {
        let url = `https://newsapi.org/v2/top-headlines?country=in&category=${this.props.category}&apiKey=acbd56ce647a4069bf7b9a5002f06ffb&page=${this.state.page + 1}&pageSize=${this.props.pageSize}`;
        this.setState({ loading: true })
        let data = await fetch(url);
        let parsedData = await data.json()
        this.setState({
            atcs: parsedData.articles,
            page: this.state.page + 1,
            loading: false
        });
    }
    render() {
        return (
            <>
                <div className="container my-3">
                    <h2>NewsChunk - Top Headlines</h2>
                    <div className="text-center">
                        {this.state.loading && <Spinner />}
                    </div>


                    <div className="row">
                        {!this.state.loading && this.state.atcs.map((element) => {
                            return <div className="col-md-4 my-3" key={element.url}>
                                <NewsItem title={element.title ? element.title : ""} description={element.description ? element.description : ""} imgUrl={element.urlToImage} newsUrl={element.url} />
                            </div>
                        })}

                    </div>
                </div>
                <div className="container d-flex justify-content-between">
                    <button disabled={this.state.page <= 1} type="button" className="btn btn-dark mb-3" onClick={this.handlePrevClick}> &larr; Previous</button>
                    <button disabled={this.state.page > Math.ceil(this.state.totalResults / this.props.pageSize)} type="button" className="btn btn-dark mb-3" onClick={this.handleNextClick}>Next &rarr; </button>
                </div>
            </>
        )
    }
}


